import '../sass/main.scss';
import makeAnimated from 'react-select/animated';
import Select from 'react-select'
import { Query } from "react-apollo";
import ApolloClient from "apollo-boost";
import { ApolloProvider } from "react-apollo";
import { AllBrandsQuery } from '../queries/tauto.queries';
import {prepareArraySelect} from '../helpers';

const customStyles = {
  input: (base) => ({
    ...base,
    height: '50px',
    maxWidth: '20px',
  }),
  singleValue: (base)=>({
    ...base,
    alignItems:'center',
    display:'flex'
  })
}

export default class FlavorForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { brands: [] };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  client = new ApolloClient({
    uri: "https://api.miautohoy.com/graphql"
  });
  handleChange(event) {
    this.setState({ selectedBrand: event.target.value });
  }

  handleSubmit(event) {
    alert('Your favorite flavor is: ' + this.state.value);
    event.preventDefault();
  }
  render() {
    return (
      <ApolloProvider client={this.client}>
        <div className="container text-center">
          <Query
            query={AllBrandsQuery}
          >
            {({ loading, error, data }) => {
              if (loading) return <p>Loading...</p>;
              if (error) return <p>Error :(</p>;

              return <form onSubmit={this.handleSubmit}>
                <label>
                  Completa los datos para obtener el precio del veh√≠culo
          <Select placeholder="Selecciona la marca" components={makeAnimated()} options={prepareArraySelect(data.AllBrands, 'ta3_nmarc', 'ta3_marca')} styles={customStyles} />
                </label>
                <button type="submit" className="btn-primary" value="Submit">Ver precio</button>
              </form>
            }}
          </Query>
        </div>
      </ApolloProvider>

    );
  }
}